'use strict';

/**
* Natural logarithm of the probability density function (PDF) for an Erlang distribution.
*
* @module @stdlib/stats-base-dists-erlang-logpdf
*
* @example
* import logpdf from '@stdlib/stats-base-dists-erlang-logpdf';
*
* var y = logpdf( 0.5, 2, 2.5 );
* // returns ~-0.111
*
* var myLogPDF = logpdf.factory( 6, 7.0 );
* y = myLogPDF( 7.0 );
* // returns ~-1.864
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
